<template>
    <div class="goods">
        <div class="menu-wrapper" v-el:menu-wrapper>
            <ul>
                <li v-for="item in goods" class="menu-item" :class="{'current':currentIndex===$index}" @click="selectMenu($index,$event)">
                    <span class="text">
                        <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
                    </span>
                </li>
            </ul>
        </div>
        <div class="foods-wrapper" v-el:foods-wrapper>
            <ul>
                <li v-for="item in goods" class="food-list food-list-hook">
                    <h1 class="title">{{item.name}}</h1>
                    <ul>
                        <li v-for="food in item.foods" class="food-item">
                            <div class="icon">
                                <img :src="food.icon" alt="" width="57px" height="57px">
                            </div>
                            <div class="content">
                                <h2 class="name">{{food.name}}</h2>
                                <p class="desc">{{food.description}}</p>
                                <div class="extra">
                                    <span class="count">月售{{food.sellCount}}份</span>
                                    <span>好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">￥{{food.price}}</span>
                                    <span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
                                </div>
                                <div class="cartcontrol-wrapper">
                                    <cartcontrol :food="food"></cartcontrol>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <!--加载组件，传递数据-->
        <shopcart :select-foods='selectFoods' :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
    </div>
</template>

<script>
    import BScroll from 'better-scroll';
    import shopcart from 'components/shopcart/shopcart';
    import cartcontrol from 'components/cartcontrol/cartcontrol';
    export default {
        props: {
            seller: {
                type: Object
            }
        },
        data() {
            return {
                goods: [],
                listHeight: [],
                scrollY: 0
            };
        },
        // 实时计算
        computed: {
            currentIndex() {
                for(let i = 0; i < this.listHeight.length; i++) {
                    let height1 = this.listHeight[i];
                    let height2 = this.listHeight[i + 1];
                    if(!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
                        return i;
                    }
                }
                return 0;
            },
            selectFoods() {
                let foods = [];
                this.goods.forEach((good) => {
                    good.foods.forEach((food) => {
                        if(food.count) {
                            foods.push(food);
                        }
                    });
                });
                return foods;
            }
        },
        created() {
            this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
            this.$http.get('/api/goods').then(response => {
                // 获取数据
                this.goods = response.body.data;
                // vue异步更新dom，dom更新接口
                this.$nextTick(() => {
                    this._initScroll();
                    this._calculateHeight();
                });
            }, response => {
                console.log("error");
            });
        },
        methods: {
            // 初始化滚动区域
            _initScroll() {
                this.meunScroll = new BScroll(this.$els.menuWrapper, {
                    click: true
                });

                this.foodsScroll = new BScroll(this.$els.foodsWrapper, {
                    click: true,
                    probeType: 3
                });
                this.foodsScroll.on('scroll', (pos) => {
                    this.scrollY = Math.abs(Math.round(pos.y));
                });
            },
            // 获取每个list高度，放入数组listHeight[]
            _calculateHeight() {
                let foodList = this.$els.foodsWrapper.getElementsByClassName('food-list-hook');
                let height = 0;
                this.listHeight.push(height);
                for(let i = 0; i < foodList.length; i++) {
                    let item = foodList[i];
                    height += item.clientHeight;
                    this.listHeight.push(height);
                }
            },
            selectMenu(index, event) {
                if(!event._constructed) {
                    return;
                }
                let foodList = this.$els.foodsWrapper.getElementsByClassName('food-list-hook');
                let el = foodList[index];
                this.foodsScroll.scrollToElement(el, 300);
            }
        },
        components: {
            shopcart,
            cartcontrol
        }
    };
</script>

<style>
    .goods{
        display: flex;
        position: absolute;
        top: 174px;
        bottom: 46px;
        width: 100%;
        overflow: hidden;
    }
    .goods .menu-wrapper{
        flex: 0 0 80px;
        width: 80px;
        background: #f3f5f7;
    }
    .goods .menu-wrapper .menu-item{
        position: relative;
        display: table;
        height: 54px;
        width: 56px;
        line-height: 14px;
        padding: 0 12px;
    }
    .goods .menu-wrapper .current{
        position: relative;
        z-index: 10;
        margin-top: -1px;
        background-color: #fff;
        font-weight: 700;
    }
    .goods .menu-wrapper .current .text{
        border: none;
    }
    .goods .menu-wrapper .menu-item::after{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: solid 1px rgba(7,17,27,0.1);
        content: '';
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
    }
    .goods .menu-wrapper .menu-item .icon{

    }
    .goods .menu-wrapper .menu-item .text{
        display: table-cell;
        width: 56px;
        vertical-align: middle;
        font-size: 12px;
    }
    .goods .foods-wrapper{
        flex: 1;
    }
    .goods .foods-wrapper .title{
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left: 2px solid #d9dde1;
        font-size: 12px;
        color: rgb(147,153,159);
        background-color: #f3f5f7;
    }
    .goods .foods-wrapper .food-item{
        position: relative;
        display: flex;
        margin: 18px;
        padding-bottom: 18px;
    }
    .goods .foods-wrapper .food-item::after{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: solid 1px rgba(7,17,27,0.1);
        content: '';
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
    }
    .goods .foods-wrapper .food-item:last-child::after{
        border-top: none;
        margin-bottom: 0;
    }
    .goods .foods-wrapper .food-item .icon{
        flex: 0 0 57px;
        margin-right: 10px;
    }
    .goods .foods-wrapper .food-item .content{
        flex: 1;
    }
    .goods .foods-wrapper .food-item .content .name{
        margin: 2px 0 8px 0;
        height: 14px;
        line-height: 14px;
        font-size: 14px;
        color: rgb(7,17,27);
    }
    .goods .foods-wrapper .food-item .content .desc{
        margin-bottom: 8px;
        line-height: 10px;
        font-size: 10px;
        color: rgb(147,153,159);
    }
    .goods .foods-wrapper .food-item .content .extra{
        line-height: 10px;
        font-size: 10px;
        color: rgb(147,153,159);
    }
    .goods .foods-wrapper .food-item .content .extra .count{
        margin-right: 12px;
    }
    .goods .foods-wrapper .food-item .content .price{
        font-weight: 700;
        line-height: 24px;
    }
    .goods .foods-wrapper .food-item .content .price .now{
        margin-right: 18px;
        font-size: 14px;
        color: rgb(240,20,20);
    }
    .goods .foods-wrapper .food-item .content .price .old{
        font-size: 10px;
        text-decoration: line-through;
        color: rgb(147,153,159);
    }
    .goods .foods-wrapper .food-item .content .cartcontrol-wrapper{
        position: absolute;
        right: 0;
        bottom: 20px;
    }
</style>



