<template>
    <div class="cartcontrol">
        <div class="cart-decrease" v-show="food.count>0" @click="decreaseCart" transition="move">
            <span class="inner">-</span>
        </div>
        <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
        <div class="cart-add" @click="addCart">+</div>
    </div>
</template>

<script>
    import Vue from 'vue';
    export default{
        props: {
            food: {
                type: Object
            }
        },
        created() {
            console.log(this.food);
        },
        methods: {
            addCart(event) {
                if(!event._constructed) {
                    return;
                }
                if(!this.food.count) {
                    Vue.set(this.food, 'count', 1);
                }else {
                    this.food.count++;
                }
            },
            decreaseCart(event) {
                if(!event._constructed) {
                    return;
                }
                if(this.food.count) {
                    this.food.count--;
                }
            }
        }
    };
</script>

<style>
    .cartcontrol .cart-decrease,.cartcontrol .cart-add{
        display: inline-block;
        width: 20px;
        height: 20px;
        line-height: 18px;
        text-align: center;
        border-radius: 50%;
        background: red;
        color: #fff;
        font-weight: 700;
    }
    .cartcontrol .cart-decrease{
        transition: all 0.4s linear;
    }
    .cartcontrol .move-transition{
        opacity: 1;
        transform: translate3D(0,0,0);
    }
    .cartcontrol .move-transition .inner{
        display: inline-block;
        transition: all 0.4s linear;
        transform: rotate(0deg);
    }
    .cartcontrol .move-enter,.cartcontrol .move-leave{
        opacity: 0;
        transform: translate3D(24px,0,0);
    }
    .cartcontrol .move-enter .inner,.cartcontrol .move-leave .inner{
        transform: rotate(360deg);
    }
    .cartcontrol .cart-count{
        display: inline-block;
    }
</style>



