<template>
  <div>
     <v-header :seller="seller"></v-header>
      <div class="tab">
          <div class="tab-item">
              <a v-link="{path:'/goods'}">商品</a>
          </div>
          <div class="tab-item">
              <a v-link="{path:'/ratings'}">评论</a>
          </div>
          <div class="tab-item">
              <a v-link="{path:'/seller'}">商家</a>
          </div>
      </div>
      <!--路由外链,传递seller数据-->
      <router-view :seller="seller"></router-view>
  </div>
</template>

<script>
    import header from 'components/header/header.vue';

    export default{
        data() {
            return {
                seller: {}
            };
        },
        created() {
            this.$http.get('/api/seller').then(response => {
                // 获取数据
                this.seller = response.body.data;
                console.log(this.seller);
            }, response => {
                console.log("error");
            });
        },
        components: {
            'v-header': header
        }
    };
</script>

<style>
    .tab{
        display: flex;
        position: relative;
        width: 100%;
        height: 40px;
        line-height: 40px;
        /*border-bottom: solid 1px rgba(7,17,27,0.1);*/
    }
    .tab::after{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: solid 1px rgba(7,17,27,0.1);
        content: '';
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
    }
    #app .tab .tab-item{
        flex: 1;
        text-align: center;
    }
    #app .tab .tab-item a{
        display: block;
        font-size: 14px;
        color: rgb(77,85,93);
    }
    #app .tab .tab-item .active{
        color: rgb(240,20,20);
    }
</style>



