<template>
    <div class="header">
        <div class="content-wrapper">
            <div class="avatar">
                <img width="64" height="64" :src="seller.avatar" alt="">
            </div>
            <div class="content">
                <div class="title">
                    <span class="brand"></span>
                    <span class="name">{{seller.name}}</span>
                </div>
                <div class="description">
                    {{seller.description}}/{{seller.deliveryTime}}分钟送达
                </div>
                <div v-if="seller.supports" class="support">
                    <span class="icon"></span>
                    <span class="text">{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div v-if="seller.supports" class="support-count">
                <span class="count">{{seller.supports.length}}个 ></span>
                <i class="icon_keyboard_arrow_right"></i>
            </div>
        </div>
        <div class="bulletin-wrapper">
            <span class="bulletin-title"></span>
            <span class="bulletin-text">{{seller.bulletin}}</span>
            <i class="icon_keyboard_arrow_right"></i>
        </div>
        <div class="background">
            <img :src="seller.avatar" alt="" width="100%" height="100%">
        </div>
    </div>
</template>

<script>
    export default {
        // 接收seller
        props: {
            seller: {
                type: Object
            }
        }
    };
</script>

<style>
    .header{
        color: #fff;
        position: relative;
        background-color: rgba(7,17,27,0.5);
    }
    .header .content-wrapper{
        position: relative;
        padding: 24px 12px 18px 24px;
        font-size: 0;
    }
    .header .content-wrapper .avatar,.header .content-wrapper .content{
        display: inline-block;
    }
    .header .content-wrapper .avatar{
        vertical-align: top;
    }
    .header .content-wrapper .avatar img{
        border-radius: 2px;
    }
    .header .content-wrapper .content{
        font-size: 14px;
        margin-left: 16px;
    }
    .header .content-wrapper .content .title{
        margin: 2px 0 8px 0;
    }
    .header .content-wrapper .content .title .brand{
        display: inline-block;
        width: 30px;
        height: 18px;
        background-image: url("brand@2x.png");
        background-size: 30px 18px;
        vertical-align: top;
    }
    .header .content-wrapper .content .title .name{
        margin-left: 6px;
        font-size: 16px;
        line-height: 18px;
        font-weight: bold;
    }
    .header .content-wrapper .content .description{
        margin-bottom: 10px;
        line-height: 12px;
        font-size: 12px;
    }
    .header .content-wrapper .content .support .icon{
        display: inline-block;
        width: 12px;
        height: 12px;
        margin-right: 4px;
        vertical-align: top;
        background: url("decrease_1@2x.png");
        background-size: 12px 12px;
    }
    .header .content-wrapper .content .support .text{
        font-size: 10px;
        line-height: 12px;
    }
    .header .content-wrapper .support-count{
        position: absolute;
        right: 12px;
        bottom: 18px;
        padding: 0 8px;
        height: 24px;
        line-height: 24px;
        border-radius: 14px;
        background: rgba(0,0,0,0.2);
        text-align: center;
    }
    .header .content-wrapper .support-count .count{
        font-size: 10px;
    }
    .header .bulletin-wrapper{
        height: 28px;
        line-height: 28px;
        padding: 0 22px 0 12px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        background: rgba(7,17,27,0.2);
    }
    .header .bulletin-wrapper .bulletin-title{
        display: inline-block;
        width: 22px;
        height: 12px;
        background: url("bulletin@2x.png") no-repeat;
        background-size: 22px 12px;
        vertical-align: top;
        margin-top: 8px;
    }
    .header .bulletin-wrapper .bulletin-text{
        font-size: 10px;
        font-weight: 200;
        margin-right: 4px;
        vertical-align: top;
    }
    .header .background{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        filter: blur(10px);
    }
</style>



